import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

let lifeData = {};

try{
	// lifeDataAPP
	lifeData = uni.getStorageSync('lifeData');
}catch(e){
	
}

// APPstate
let saveStateKeys = ['vuex_user','wxBannerAdIds','wxRewardedAdIds','wxInterstitialAdIds','wxVideoAdIds','wxVideoPatchAdIds','wxCustomPolylattice2VerticalAdIds'];

// 
const saveLifeData = function(key, value){
	// 
	if(saveStateKeys.indexOf(key) != -1) {
		// lifeData
		let tmp = uni.getStorageSync('lifeData');
		// APPlifeData{}
		tmp = tmp ? tmp : {};
		tmp[key] = value;
		// lifeData
		uni.setStorageSync('lifeData', tmp);
	}
}
const store = new Vuex.Store({
	state: {
		// lifeDatastate
		// vuex_
		appcode: 1001,
		showWxLogin: false,//
		vuex_user: lifeData.vuex_user ? lifeData.vuex_user : '',
		wxBannerAdIds: lifeData.wxBannerAdIds || [],//bannerid
		wxRewardedAdIds: lifeData.wxRewardedAdIds || [],//
		wxInterstitialAdIds: lifeData.wxInterstitialAdIds || [],//ID
		wxVideoAdIds: lifeData.wxVideoAdIds || [],//ID
		wxVideoPatchAdIds: lifeData.wxVideoPatchAdIds || [],//ID
		wxCustomPolylattice2VerticalAdIds: lifeData.wxCustomPolylattice2VerticalAdIds || [],//2
	},
	mutations: {
		$uStore(state, payload) {
			// stateuser.info.score = 1
			let nameArr = payload.name.split('.');
			let saveKey = '';
			let len = nameArr.length;
			if(len >= 2) {
				let obj = state[nameArr[0]];
				for(let i = 1; i < len - 1; i ++) {
					obj = obj[nameArr[i]];
				}
				obj[nameArr[len - 1]] = payload.value;
				saveKey = nameArr[0];
			} else {
				// state
				state[payload.name] = payload.value;
				saveKey = payload.name;
			}
			// 
			saveLifeData(saveKey, state[saveKey])
		}
	}
})

export default store
