import siteinfo from "../siteinfo.js"
module.exports = (vm) => {
	// 
	uni.$u.http.setConfig((config) => {
		config.timeout = 600000;
		/* config */
		config.baseURL = siteinfo.siteroot; /*  */
		config.wxapp_id = siteinfo.uniacid
		return config
	})
	// 
	uni.$u.http.interceptors.request.use((config) => { // async await 
		// dataundefined{}
		config.data = config.data || {}
		// customtoken
		// if(config?.custom?.auth) {
		// 	// vmvuexvm.$store.state
		// 	config.header.token = vm.$store.state.userInfo.token
		// } b5e8a700ac9739a43eae0c83aca7b7b4
		const token = uni.getStorageSync('token');
		// const token = '6a1f1f6c7f43d854ba779de1dd4765f5';
		config.header.token = token;
		return config
	}, config => { // async await 
		return Promise.reject(config)
	})

	// 
	uni.$u.http.interceptors.response.use((response) => {
		/*  async await */
		const data = response.data
		// 
		const custom = response.config?.custom
		let pages = getCurrentPages()

		return data.data === undefined ? {} : data
	}, (response) => {
		//  statusCode !== 200
		return Promise.reject(response)
	})
}
